
import Vue from "vue";
import Editor from "@/components/form-components/Editor.vue";
import rules from "@/services/helpers/validation-rules";

export default Vue.extend({
  components: { Editor },

  name: "ExpertConclusion",

  props: {
    content: {
      type: Object,
      required: true,
      default: () => ({})
    },
    documents: {
      type: Array,
      required: true,
      default: () => []
    }
  },

  data: () => ({
    rules,
    selects: {
      categories: [] as Array<SelectComponentInterface>
    } as any,
    subcategories: {} as any,
    forceKey: 0 as number,
    toEdit: false as boolean,
    toCancel: false as boolean
  }),

  async mounted() {
    await this.loadData();
  },

  methods: {
    async loadData(): Promise<void> {
      try {
        this.selects.categories = await this.$API
          .goodsCategories()
          .getParentsList();
      } catch (e) {
        await this.$store.dispatch("alert/showError", e.message || e);
      }
    },
    forceRender(): void {
      this.forceKey = this.forceKey++;
    },
    deleteRow(index: number): void {
      this.documents.splice(index, 1);
    },
    addRow(): void {
      this.documents.push({ file_type: "conclusion" });
    },
    async submit(): Promise<void> {
      this.$emit("submit");

      if (this.toCancel) {
        await this.$router.push("/orders");
      }
    },
    setContent(event: any): void {
      this.$emit("setContent", event);
    }
  }
});
