
import Vue from "vue";
import KothingEditor from "kothing-editor";
import kothingConfig from "../config/editorConfig.json";
import plugins from "kothing-editor/lib/plugins";
import lang from "kothing-editor/lib/lang";
import "kothing-editor/dist/css/kothing-editor.min.css";
import rules from "@/services/helpers/validation-rules";

export default Vue.extend({
  name: "Editor",

  props: {
    editorId: {
      required: false,
      type: String,
      default: "editor"
    },
    content: {
      required: false,
      type: Object,
      default: () => ({})
    },
    single: {
      required: false,
      type: Boolean,
      default: false
    },
    disabled: {
      required: false,
      type: Boolean,
      default: false
    },
    mode: {
      required: false,
      default: "classic"
    }
  },

  data: () => ({
    rules,
    kothingConfig,
    item: {} as any,
    editor_en: "" as any,
    editor_ro: "" as any,
    editor_ru: "" as any,
    editor: "" as any,
    currentTab: 0 as number,
    userToSendEmailTo: null as number | null,
    languages: ["en", "ro", "ru"] as Array<string>,
    flags: {
      en: require("@/assets/images/locales/en.svg"),
      ro: require("@/assets/images/locales/ro.svg"),
      ru: require("@/assets/images/locales/ru.svg")
    } as any,
    subjects: {} as any,
    editorRequirements: [[], [], []] as Array<Array<any>>,
    missingKeys: [
      [] as Array<string>,
      [] as Array<string>,
      [] as Array<string>
    ] as any,
    selects: {
      users: [] as Array<any>,
      services: [] as Array<any>
    } as any,
    errorMessages: {} as { [value: string]: Array<string> },
    loading: false as boolean,
    forceKey: 0 as number
  }),

  computed: {
    services() {
      return this.selects.services;
    }
  },

  mounted() {
    this.createEditor();
    this.setEditorsContent();
  },

  methods: {
    async submit(): Promise<void> {
      this.loading = true;
      try {
        this.setNewEditorContent();
        if (this.passedValidation()) {
          await this.$API.documentTemplates().create(this.item);
          await this.$router.push("/orders");
          await this.$store.dispatch(
            "alert/showSuccess",
            this.$t("document-templates.modals.alert.successful-edit")
          );
        }
      } catch (e) {
        if (e.hasOwnProperty("errors")) {
          this.errorMessages = e.errors;
          setTimeout(() => {
            this.errorMessages = {};
          }, 2000);
        }
        await this.$store.dispatch("alert/showError", e.message);
      }
      this.loading = false;
    },
    createEditor(): void {
      if (!this.single) {
        const language = this.$store.getters["localization/getCurrent"];
        this.editor_en = KothingEditor.create(`${this.editorId}_en`, {
          plugins: plugins,
          lang: (lang as any)[language],
          ...this.kothingConfig
        });
        this.editor_ro = KothingEditor.create(`${this.editorId}_ro`, {
          plugins: plugins,
          lang: (lang as any)[language],
          ...this.kothingConfig
        });
        this.editor_ru = KothingEditor.create(`${this.editorId}_ru`, {
          plugins: plugins,
          lang: (lang as any)[language],
          ...this.kothingConfig
        });
      } else {
        this.editor = KothingEditor.create(`${this.editorId}`, {
          plugins: plugins,
          mode: this.mode as any,
          ...this.kothingConfig
        });
      }
    },
    contains(target: any, pattern: Array<string>, index: number): boolean {
      let passed = true;
      for (const item of pattern) {
        if (!target.includes(item)) {
          this.missingKeys[index].push(item);
          passed = false;
        }
      }
      return passed;
    },
    passedValidation(): boolean {
      let passed = true;
      this.missingKeys = [[], [], []];

      for (const lang of this.languages) {
        if (!this.item[`content_${lang}`] || !this.item[`name_${lang}`]) {
          this.missingKeys[this.languages.indexOf(lang)].push(1);
          passed = false;
        }
      }

      return passed;
    },
    setEditorsContent(): void {
      if (!this.single) {
        for (const item of this.languages) {
          this[`editor_${item}`].setContents(this.item[`content_${item}`]);
          this.subjects[`${item}`] = this.item[`name_${item}`];
        }
      } else {
        this.editor.setContents(this.content.content);

        // this.editor.onChange = (contents: string) => {
        //   this.setContent(contents);
        // };
      }
    },
    setNewEditorContent(): void {
      for (const item of this.languages) {
        this.item[`content_${item}`] = this[`editor_${item}`].getContents();
      }
    },
    setContent(event: any) {
      this.$emit("content", event);
    }
  }
});
