
import Vue from "vue";
import Editor from "@/components/form-components/Editor.vue";
import rules from "@/services/helpers/validation-rules";

export default Vue.extend({
  components: { Editor },

  name: "Examination",

  props: {
    items: {
      type: Array,
      default: () => [],
      required: true
    },
    errorMessages: {
      type: Object,
      default: () => ({}),
      required: false
    },
    content: {
      type: Object,
      default: () => ({}),
      required: true
    }
  },

  data: () => ({
    rules,
    localItems: [] as Array<any>,
    toCancel: false as boolean
  }),

  watch: {
    items: {
      deep: true,
      immediate: true,
      handler() {
        this.localItems = this.items;
      }
    },
    localItems: {
      deep: true,
      immediate: true,
      handler() {
        this.$emit("update", this.localItems);
      }
    }
  },

  methods: {
    deleteRow(index: number): void {
      this.items.splice(index, 1);
    },
    addRow(): void {
      this.items.push({});
    },
    async submit(): Promise<void> {
      this.$emit("submit");

      if (this.toCancel) {
        await this.$router.push("/orders");
      }
    },
    setContent(event: any): void {
      this.$emit("setContent", event);
    }
  }
});
